import { defineStore } from "pinia";
import { ref } from "vue";
import { ProductFilter } from "./products";

const usePromoStore = defineStore("promo", () => {
  const promo = ref({
    "": ProductFilter.COLLAGEN,
    "-3": ProductFilter.OMEGA,
    " 3": ProductFilter.VITAMINS,
    "": ProductFilter.SQUALENE,
    "  ": ProductFilter.KIDS,
});

  return { promo };
});

export default usePromoStore;
